import React, { useEffect, useRef } from 'react';
import mapboxgl from 'mapbox-gl';
import 'mapbox-gl/dist/mapbox-gl.css';
import { Zone } from '../hooks/useZones';
import { LiveLocation } from '../types';

// --- CONFIGURATION ---
mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN || 'pk.eyJ1IjoibmlraGlsMjEwMjA2IiwiYSI6ImNta2U0NG0zdTAzMzUzZXMwZjZwbXFzZ3kifQ.fgjpDhGp_9bUapwaLEvtsg';

// Mock Patrol Routes (Lines)
const PATROL_ROUTES = [
  {
    id: 'route-alpha',
    coordinates: [
      [79.1559, 12.9692],
      [79.1580, 12.9720],
      [79.1620, 12.9750],
      [79.1500, 12.9700]
    ]
  }
];

// Helper: Calculate distance to check Red Zone interesection (Simple Haversine or Euclidean for small areas)
function getDistanceFromLatLonInKm(lat1: number, lon1: number, lat2: number, lon2: number) {
  const R = 6371; // Radius of the earth in km
  const dLat = deg2rad(lat2 - lat1);
  const dLon = deg2rad(lon2 - lon1);
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2)
    ;
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const d = R * c; // Distance in km
  return d;
}

function deg2rad(deg: number) {
  return deg * (Math.PI / 180);
}

interface MapViewProps {
  zones?: Zone[];
  locations?: LiveLocation[];
  showPatrolRoutes?: boolean;
}

export const MapView: React.FC<MapViewProps> = ({
  zones = [],
  locations = [],
  showPatrolRoutes = false
}) => {
  const mapContainer = useRef<HTMLDivElement>(null);
  const map = useRef<mapboxgl.Map | null>(null);
  const markersRef = useRef<mapboxgl.Marker[]>([]);

  // Initialize Map
  useEffect(() => {
    if (map.current || !mapContainer.current) return;

    map.current = new mapboxgl.Map({
      container: mapContainer.current,
      style: 'mapbox://styles/mapbox/dark-v11',
      center: [79.1559, 12.9692], // VIT Vellore
      zoom: 14,
      pitch: 45,
      attributionControl: false
    });

    map.current.on('load', () => {
      // Initialize Sources
      map.current?.addSource('zones', {
        type: 'geojson',
        data: { type: 'FeatureCollection', features: [] }
      });

      map.current?.addSource('patrols', {
        type: 'geojson',
        data: { type: 'FeatureCollection', features: [] }
      });

      // --- 1. PATROL LINES ---
      // Dashed Neon Lines
      map.current?.addLayer({
        id: 'patrol-lines',
        type: 'line',
        source: 'patrols',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        paint: {
          'line-color': '#00F0FF', // Cyan
          'line-width': 3,
          'line-dasharray': [2, 2],
          'line-opacity': 0.7
        }
      });

      // Glow for Patrol Lines
      map.current?.addLayer({
        id: 'patrol-lines-glow',
        type: 'line',
        source: 'patrols',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        paint: {
          'line-color': '#00F0FF',
          'line-width': 8,
          'line-blur': 4,
          'line-opacity': 0.3
        }
      });

      // --- 2. ZONES (BEACON STYLE) ---
      // Red Zones Glow
      map.current?.addLayer({
        id: 'zones-glow-high',
        type: 'circle',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'HIGH'],
        paint: {
          'circle-radius': ['get', 'radius_px'], // Dynamic radius? Or mock large
          'circle-color': '#FF4D4D',
          'circle-opacity': 0.15,
          'circle-blur': 0.8
        }
      });

      // Red Zones Core
      map.current?.addLayer({
        id: 'zones-core-high',
        type: 'circle',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'HIGH'],
        paint: {
          'circle-radius': 4,
          'circle-color': '#FFFFFF',
          'circle-stroke-color': '#FF4D4D',
          'circle-stroke-width': 3
        }
      });

      // Moderate Zones Glow (Yellow)
      map.current?.addLayer({
        id: 'zones-glow-mod',
        type: 'circle',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'MODERATE'],
        paint: {
          'circle-radius': 60,
          'circle-color': '#FFD700',
          'circle-opacity': 0.15,
          'circle-blur': 0.8
        }
      });

      map.current?.addLayer({
        id: 'zones-core-mod',
        type: 'circle',
        source: 'zones',
        filter: ['==', ['get', 'severity'], 'MODERATE'],
        paint: {
          'circle-radius': 4,
          'circle-color': '#FFFFFF',
          'circle-stroke-color': '#FFD700',
          'circle-stroke-width': 3
        }
      });
    });

    return () => {
      map.current?.remove();
      map.current = null;
    }
  }, []);

  // Update Data Sources
  useEffect(() => {
    if (!map.current || !map.current.isStyleLoaded()) return;

    // 1. Zones
    const zoneFeatures = zones.map(zone => ({
      type: 'Feature',
      geometry: { type: 'Point', coordinates: [zone.lng, zone.lat] },
      properties: {
        severity: zone.severity,
        radius_px: (zone.radius || 200) / 2 // Rough pixel mapping for visual glow
      }
    }));

    (map.current.getSource('zones') as mapboxgl.GeoJSONSource)?.setData({
      type: 'FeatureCollection',
      features: zoneFeatures as any
    });

    // 2. Patrol Routes
    const patrolFeatures = showPatrolRoutes ? PATROL_ROUTES.map(route => ({
      type: 'Feature',
      geometry: { type: 'LineString', coordinates: route.coordinates },
      properties: {}
    })) : [];

    (map.current.getSource('patrols') as mapboxgl.GeoJSONSource)?.setData({
      type: 'FeatureCollection',
      features: patrolFeatures as any
    });

  }, [zones, showPatrolRoutes]);

  // Update User Markers (Beacons)
  useEffect(() => {
    if (!map.current) return;

    // Clear existing
    markersRef.current.forEach(m => m.remove());
    markersRef.current = [];

    locations.forEach(user => {
      // Check Red Zone Intersection
      // Simple logic: if user is within X meters of any HIGH severity zone
      let inRedZone = false;
      zones.forEach(z => {
        if (z.severity === 'HIGH') {
          const dist = getDistanceFromLatLonInKm(user.latitude, user.longitude, z.lat, z.lng);
          // Assuming radius is ~200m (0.2km)
          if (dist < 0.2) inRedZone = true;
        }
      });

      const el = document.createElement('div');

      // Google Maps Style Directional Beacon
      // If heading exists, rotate an arrow/cone in that direction
      // If not, just a pulsing dot

      const width = inRedZone ? '24px' : '16px';
      const height = inRedZone ? '24px' : '16px';
      const color = inRedZone ? '#FF4D4D' : '#3B82F6';

      // Base styles
      el.style.width = width;
      el.style.height = height;
      el.style.borderRadius = '50%';
      el.style.backgroundColor = color;
      el.style.border = '2px solid white';
      el.style.boxShadow = `0 0 15px ${color}`;
      el.style.transition = 'all 0.3s ease';

      if (inRedZone) {
        el.className = 'marker-danger'; // Uses the global CSS pulse
      } else {
        el.className = 'marker-safe';
      }

      // Add direction indicator if heading is present
      if (user.heading !== null && user.heading !== undefined) {
        const arrow = document.createElement('div');
        arrow.style.position = 'absolute';
        arrow.style.top = '50%';
        arrow.style.left = '50%';
        arrow.style.width = '0';
        arrow.style.height = '0';
        arrow.style.borderLeft = '6px solid transparent';
        arrow.style.borderRight = '6px solid transparent';
        arrow.style.borderBottom = `10px solid white`; // White arrow
        arrow.style.transform = `translate(-50%, -50%) rotate(${user.heading}deg) translateY(-12px)`; // Offset to point out
        arrow.style.transformOrigin = 'center bottom';
        el.appendChild(arrow);
      }

      const marker = new mapboxgl.Marker({ element: el })
        .setLngLat([user.longitude, user.latitude])
        .setPopup(new mapboxgl.Popup({ offset: 25, className: 'neo-glass text-black' }).setHTML(`
          <div style="color:black; font-weight:bold;">Unit: ${user.user_id}</div>
          <div style="color:#555;">Status: ${inRedZone ? 'DANGER' : 'Patrolling'}</div>
        `))
        .addTo(map.current!);

      markersRef.current.push(marker);
    });

  }, [locations, zones]);

  return <div ref={mapContainer} style={{ width: '100%', height: '100%' }} />;
}
