"use client";

import React, { useState, useId } from 'react';
import { MapView } from './MapView';
import { Map, Shield, Users, Eclipse as EclipseIcon, X as XIcon, UserRound as UserRoundIcon } from 'lucide-react';
import { useZones } from '../hooks/useZones';
import { useRealtimeLocations } from '../hooks/useRealtimeLocations';
import { supabase } from '../lib/supabaseClient';
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";

// --- COMPONENTS ---

const NavButton = ({ icon, active, onClick }: { icon: React.ReactNode, active: boolean, onClick: () => void }) => (
  <button
    onClick={onClick}
    className={`w-full aspect-square rounded-xl flex items-center justify-center transition-all duration-300 ${active
        ? 'bg-[#1A0505] text-[#FF4D4D] border border-[#FF4D4D]/30 shadow-[0_0_15px_rgba(255,77,77,0.15)]'
        : 'text-[#444] hover:text-[#EDEDED] hover:bg-[#111]'
      }`}
  >
    {icon}
  </button>
);

function SystemBanner() {
  const [isVisible, setIsVisible] = useState(true);
  if (!isVisible) return null;

  return (
    <div className="neo-glass px-4 py-3 text-white mb-4 rounded-xl pointer-events-auto">
      <div className="flex gap-2 items-center">
        <div className="flex grow gap-3 items-center">
          <EclipseIcon aria-hidden="true" className="shrink-0 opacity-60 text-[#00F0FF]" size={16} />
          <div className="flex grow flex-col justify-between gap-1">
            <p className="text-sm font-medium">System Online</p>
            <p className="text-xs text-zinc-400">All beacons operational.</p>
          </div>
        </div>
        <Button
          aria-label="Close banner"
          className="group -my-1.5 -me-2 size-8 shrink-0 p-0 hover:bg-white/5 text-white"
          onClick={() => setIsVisible(false)}
          variant="ghost"
        >
          <XIcon aria-hidden="true" className="opacity-60 transition-opacity group-hover:opacity-100" size={16} />
        </Button>
      </div>
    </div>
  );
}

function ThemeToggle() {
  const id = useId();
  const [checked, setChecked] = useState<boolean>(true); // Default to Dark (On)

  return (
    <div className="inline-flex items-center gap-2 pointer-events-auto bg-[#0A0A0A] p-2 rounded-full border border-white/10">
      <Switch
        aria-label="Toggle switch"
        checked={checked}
        id={id}
        onCheckedChange={setChecked}
        className="data-[state=checked]:bg-[#FF4D4D]"
      />
      <Label className="font-medium text-xs text-zinc-400 cursor-pointer" htmlFor={id}>
        {checked ? "Dark Mode" : "Light Mode"}
      </Label>
    </div>
  );
}

function UserProfile() {
  return (
    <Avatar className="h-10 w-10 border-2 border-[#FF4D4D]/20">
      <AvatarFallback className="bg-[#1A0505] text-[#FF4D4D]">
        <UserRoundIcon aria-hidden="true" className="opacity-80" size={20} />
      </AvatarFallback>
    </Avatar>
  );
}

// --- MAIN DASHBOARD ---

export default function TacticalDashboard() {
  const [activeTab, setActiveTab] = useState('map');

  // Real Data Hooks
  const { zones } = useZones();
  const { locations } = useRealtimeLocations(supabase);

  return (
    // MASTER GRID: 90px Sidebar | Remaining Space for Map
    <div className="h-screen w-screen grid grid-cols-[90px_1fr] bg-[#050505] overflow-hidden text-white font-sans">

      {/* COL 1: THE DOCK */}
      <aside className="border-r border-[#222] bg-[#0A0A0A] flex flex-col items-center py-8 z-50 shadow-2xl justify-between h-full">
        <div className="flex flex-col items-center w-full gap-8">
          {/* Brand Beacon */}
          <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-[#FF4D4D] to-[#990000] shadow-[0_0_30px_rgba(255,0,0,0.4)] flex items-center justify-center shrink-0">
            <span className="font-bold text-black text-xs">CMD</span>
          </div>

          {/* Navigation Stack */}
          <nav className="flex flex-col gap-6 w-full px-4">
            <NavButton icon={<Map size={24} />} active={activeTab === 'map'} onClick={() => setActiveTab('map')} />
            <NavButton icon={<Shield size={24} />} active={activeTab === 'patrol'} onClick={() => setActiveTab('patrol')} />
            <NavButton icon={<Users size={24} />} active={activeTab === 'users'} onClick={() => setActiveTab('users')} />
          </nav>
        </div>

        {/* User Profile at Bottom */}
        <div className="mb-4">
          <UserProfile />
        </div>
      </aside>

      {/* COL 2: THE THEATER */}
      <main className="relative h-full w-full">
        {/* LAYER A: The Map Engine (Absolute Fill) */}
        <div className="absolute inset-0 z-0">
          <MapView
            zones={zones}
            locations={locations}
            showPatrolRoutes={activeTab === 'patrol' || activeTab === 'map'}
          />
        </div>

        {/* LAYER B: The HUD Overlay */}
        <div className="absolute top-6 right-6 z-10 w-80 flex flex-col gap-4 pointer-events-none">
          <SystemBanner />

          <div className="flex justify-end">
            <ThemeToggle />
          </div>

          {/* Simple Status Card */}
          <div className="neo-glass p-4 rounded-xl flex items-center justify-between pointer-events-auto">
            <div className="flex items-center gap-3">
              <div className="w-2 h-2 rounded-full bg-[#00F0FF] animate-pulse" />
              <span className="text-xs font-mono text-[#00F0FF] tracking-widest">SentraBeacon NET</span>
            </div>
            <span className="text-xs text-[#666] font-mono">
              {locations.length > 0 ? `${locations.length} ACTIVE` : 'SEARCHING...'}
            </span>
          </div>
        </div>
      </main>
    </div>
  );
}
